import React, { Component } from 'react';
import Menu from './components/Menu.js'
import Logos from './components/Logos.js'
import Cool from './components/Cool.js'

import Typed from 'typed.js'
import { ProgressBar,Container,Carousel,Jumbotron} from 'react-bootstrap';
import './App.css';

import img1 from './images/1.jpg'
import img2 from './images/2.jpg'
import img3 from './images/4.jpg'


class App extends Component {
  constructor(){
    super()
    this.state={
      showLogos:true,
      showWhoAmI:true,
    }
    this.showLogos=this.showLogos.bind(this)
    this.showWhoAmI=this.showWhoAmI.bind(this)
  }
  componentDidMount(){
    console.log();
    var typed = new Typed('.typing', {
      strings: ["Hello", "Welcom."],
      typeSpeed: 30
    });

    // this.effect = window.VANTA.RINGS({
    //         el: ".App"
    // })
    // this.effect = window.VANTA.CELLS({
    //         el: ".navbar"
    // })
    //
    // this.effect = window.VANTA.WAVES({
    //         el: ".jumbotron",
    //         color: 0x0,
    //          shininess: 148.00,
    //          waveHeight: 40.00,
    //          waveSpeed: 2.00,
    //          zoom: 0.73
    //
    //
    // })


    setInterval(()=>{
        const min=1
        const max=100
        this.setState(state=>({
          pl1:Math.floor(Math.random()*(max-min+1)+min),
          pl2:Math.floor(Math.random()*(max-min+1)+min),
          pl3:Math.floor(Math.random()*(max-min+1)+min),
        }))
    },500)
  }
  showLogos(){
    this.setState(state=>({
      showLogos:!state.showLogos,
    }))
  }
  showWhoAmI(){
    this.setState(state=>({
      showWhoAmI:!state.showWhoAmI,
    }))
  }
  render() {
    return (
      <Container id="App" className="App">
        <Cool />
      <Menu showWhoAmI={this.showWhoAmI} showLogos={this.showLogos} />

    <Jumbotron  style={{display:this.state.showWhoAmI?"block":"none"}}>
      <div className="typing d-inline">
      </div>
      <Logos className="w-20 d-inline" images={[img1,img2,img3]}/>
    </Jumbotron>
      <div className="d-inline-flex flex-row justify-content-end">
        <div onClick={this.showLogos} style={{display:this.state.showLogos?"block":"none"}} className="">

        </div>
      </div>
      <div className="pb-5">
        <ProgressBar style={{backgroundColor:"#265078"}}>
          <ProgressBar variant="success" now={this.state.pl1} key={1} />
          <ProgressBar variant="warning" now={this.state.pl2} key={2} />
          <ProgressBar variant="danger" now={this.state.pl3} key={3} />
        </ProgressBar>
        <ProgressBar style={{backgroundColor:"#265078"}} striped variant="red" now={this.state.pl1} />
        <ProgressBar style={{backgroundColor:"#265078"}} striped variant="info" now={this.state.pl2} />
        <ProgressBar style={{backgroundColor:"#265078"}} striped variant="warning" now={this.state.pl3} />
        <ProgressBar style={{backgroundColor:"#265078"}} striped variant="danger" now={this.state.pl2} />
      </div>

      <Carousel>
      <Carousel.Item>
        <img
          className="d-block w-100"
          src="https://source.unsplash.com/200x80/?ReactJS=1"
          alt="First slide"
        />
        <Carousel.Caption>
          <h3>ReactJS</h3>
          <p>Basic coding knowlage</p>
        </Carousel.Caption>
      </Carousel.Item>
      <Carousel.Item>
        <img
          className="d-block w-100"
          src="https://source.unsplash.com/200x80/?JavaScript=1"
          alt="Third slide"
        />

        <Carousel.Caption>
          <h3>JavaScript</h3>
          <p>Good knowlage</p>
        </Carousel.Caption>
      </Carousel.Item>
      <Carousel.Item>
        <img
          className="d-block w-100"
          src="https://source.unsplash.com/200x80/?html5=8"
          alt="Third slide"
        />

        <Carousel.Caption>
          <h3>html5</h3>
        </Carousel.Caption>
      </Carousel.Item>
      </Carousel>

      </Container>

    );
  }
}

export default App;
