import React, { Component} from 'react'
import Zoom from 'react-reveal/Zoom'
import { Image } from 'react-bootstrap';
import img1 from '../images/1.jpg'

export default class Logos extends Component{
      constructor(props){
        super(props)
        this.state={
          show:true,
          delay:3000,
        }
      }
      change(src){
          this.setState(state=>({show:state.show?false:true,src:src}),()=>{
            this.setState(state=>({show:state.show?false:true}))
          })
      }

      loop(){
        const logos=this.props.images?this.props.images:
        [
          img1,
          'https://source.unsplash.com/200x200/?JavaScript=1',
          'https://source.unsplash.com/200x200/?php=2',
          'https://source.unsplash.com/200x200/?webmaster=3',
          'https://source.unsplash.com/200x200/?angular=4',
          'https://source.unsplash.com/200x200/?code=5',
        ]

        logos.map(async (url,index)=>{
          const delay=this.state.delay;
          setTimeout(()=>{
            if (index===logos.length-1){
              this.loop()
            }
            this.change(url)
          },delay+(index*delay));
        })

      }

      componentDidMount(){
          this.loop();
      }

      render() {
      return (
              <Zoom opposite duration={this.state.delay} when={this.state.show}>
                <Image  roundedCircle width="120" src={this.state.src} />
             </Zoom>
           )
         }
      }
